@font-face {
  font-family: "Raleway";
  src: url("/src/fonts/raleway/raleway-800.woff2") format("woff2");
  font-style: normal;
  font-weight: 800;
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  src: url("/src/fonts/rubik/rubik-400.woff2") format("woff2");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  src: url("/src/fonts/rubik/rubik-700.woff2") format("woff2");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
body {
  margin: 0;
}
h1,
h2,
h3 {
  font-weight: 400;
  margin: 0;
}
p,
dd {
  margin: 0;
}
ul,
ol {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
a {
  text-decoration: none;
  display: inline-block;
}
button {
  padding: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
}
.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  padding-top: 40px;
  background-color: #ffffff;
}
.page__main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  row-gap: 70px;
}
.body__wrapper-content {
  width: 1160px;
  margin: 0 auto;
}
.header {
  background-color: #ffffff;
}
.header__logo {
  align-self: flex-start;
}
.header__wrapper {
  box-sizing: border-box;
  padding: 0 60px 109px;
  background-color: #ffe17f;
}
.header__nav {
  position: relative;
  bottom: 20px;
  display: flex;
  flex-direction: column;
  row-gap: 30px;
}
.main-nav__user-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.main-nav__wrapper-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.main-nav__menu {
  display: flex;
  column-gap: 45px;
  flex-wrap: wrap;
}
.user-menu__search-form {
  display: flex;
}
.user-menu__search-item {
  font-family: "Rubik", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
  background-color: transparent;
  border: 2px solid #000000;
}
.user-menu__button {
  font-family: "Rubik", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  color: #000000;
  border: none;
  background-color: transparent;
}
.search-form {
  width: 517px;
}
.search-form__input {
  flex-grow: 1;
  padding-left: 48px;
  border-radius: 50px 0 0 50px;
}
.search-form__button {
  padding: 8px 56px;
  border-radius: 0 50px 50px 0;
  border-left: none;
}
.login-button {
  padding: 0 150px 0 27px;
}
.compare-button {
  padding-left: 22px;
}
.cart-button {
  padding-left: 25px;
}
.catalog-submenu__toggle {
  border: none;
  background-color: transparent;
  font-family: "Raleway", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  color: #000000;
  padding: 15px 57px 15px 0;
}
.catalog-submenu__list {
  display: none;
}
.catalog-submenu__link {
  font-family: "Rubik", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #000000;
}
.main-menu__link {
  font-family: "Raleway", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  color: #000000;
  padding: 15px 0;
}
.promo__wrapper {
  position: relative;
}
.promo-slider__number {
  font-family: "Rubik", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 180px;
  line-height: 30px;
  color: #ffffff;
  position: absolute;
  right: 49px;
  top: -68px;
}
.promo-slider__card-product {
  display: flex;
  column-gap: 40px;
}
.promo-card__slogan {
  font-family: "Raleway", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 50px;
  line-height: 50px;
  color: #000000;
  margin-bottom: 29px;
}
.promo-card__desc {
  font-family: "Rubik", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  color: #444444;
  margin-bottom: 50px;
}
.promo-card__button {
  margin-bottom: 50px;
}
.promo-card__product-option {
  display: flex;
  flex-wrap: wrap;
  column-gap: 20px;
}
.promo-card__option-item {
  display: flex;
  flex-direction: column;
  row-gap: 12px;
}
.promo-card__product-value {
  font-family: "Rubik", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 30px;
  color: #000000;
}
.promo-card__product-desc {
  font-family: "Rubik", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  color: #444444;
}
.button {
  font-family: "Raleway", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  color: #000000;
  border: none;
  background-color: transparent;
}
.catalog-category__list {
  display: flex;
  column-gap: 40px;
}
.catalog-category__item {
  width: 160px;
}
.catalog-category__link {
  display: block;
}
.catalog-category__icon {
  width: 100%;
  height: 160px;
  background-color: #ffe17f;
  margin-bottom: 32px;
}
.catalog-category__title {
  font-family: "Raleway", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.02em;
  color: #000000;
}
.service {
  background-color: #f0f0f0;
}
.service__wrapper {
  display: flex;
  align-items: center;
}
.service__button-wrapper {
  width: 280px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 35px;
  padding: 64px 0;
  margin-right: 116px;
}
.service__button {
  display: block;
  width: 160px;
  padding: 10px 0 10px 21px;
}
.service__button--current {
  width: 100%;
  text-align: start;
  background-color: #000000;
  color: #ffe17f;
}
.service__title {
  font-family: "Raleway", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 50px;
  line-height: 50px;
  color: #000000;
  margin-bottom: 34px;
}
.service__desc {
  width: 452px;
  font-family: "Rubik", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  color: #444444;
}
.delivery__wraper {
  background-color: #f0f0f0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.delivery__icon {
  background-color: #ffe17f;
  min-width: 125px;
  min-height: 100px;
}
.delivery__slogan {
  font-family: "Raleway", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 20px;
  line-height: 23px;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  color: #000000;
}
.delivery__button {
  min-width: 125px;
  min-height: 100px;
}
.—Åompany-info {
  background-color: #ffffff;
}
.—Åompany-info__wrapper {
  display: flex;
  column-gap: 108px;
}
.—Åompany-info__container {
  min-width: 520px;
  padding-top: 47px;
}
.—Åompany-info__title {
  font-family: "Raleway", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 50px;
  line-height: 50px;
  color: #000000;
  margin-bottom: 48px;
}
.—Åompany-info__container-desc {
  display: flex;
  flex-direction: column;
  row-gap: 30px;
}
.—Åompany-info__desc {
  font-family: "Rubik", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  color: #444444;
}
.—Åompany-info__delivery {
  display: flex;
  flex-direction: column;
  row-gap: 30px;
}
.—Åompany-info__subtitle {
  font-family: "Rubik", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 30px;
  color: #444444;
  margin-bottom: 15px;
}
.—Åompany-info__button {
  align-self: flex-start;
}
.transport-company__text {
  font-family: "Rubik", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  color: #444444;
}
.transport-company__list {
  display: flex;
  flex-direction: column;
  row-gap: 19px;
}
.transport-company__item {
  font-family: "Rubik", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  letter-spacing: -0.02em;
  color: #000000;
  padding-left: 34px;
}
.store-address__phone {
  text-decoration-line: underline;
}
.subscribe {
  background-color: #f0f0f0;
}
.subscribe__wrapper {
  padding: 64px 0 56px;
}
.subscribe__wpapper-text {
  display: flex;
  align-items: flex-end;
  column-gap: 252px;
  margin-bottom: 144px;
}
.subscribe__title {
  font-family: "Raleway", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 50px;
  line-height: 50px;
  color: #000000;
  margin-left: 7px;
  width: 348px;
}
.subscribe__desc {
  font-family: "Rubik", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  color: #444444;
  width: 560px;
}
.subscribe__info {
  font-family: "Rubik", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  color: #444444;
  padding-left: 32px;
}
.subscribe__wrapper-form {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.subscribe__form {
  display: flex;
}
.subscribe-form {
  width: 810px;
}
.subscribe-form__label {
  flex-grow: 1;
}
.subscribe-form__input {
  border: none;
  background-color: transparent;
  border-bottom: 3px solid #c4c4c4;
}
.subscribe-form__button {
  padding: 18px 39px;
  border: 3px solid #c4c4c4;
}
.button-accessory {
  font-family: "Raleway", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  color: #000000;
  background-color: transparent;
}
.footer {
  background-color: #444444;
}
.delivery-modal {
  display: none;
}
